'use client';
import React, { useEffect, useState } from 'react';
import Link from 'next/link';

export default function HeaderClient() {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => setScrolled(window.scrollY > 50);
    onScroll();
    window.addEventListener('scroll', onScroll);
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  // Smooth scroll for internal anchors (if present)
  const handleAnchor = (e: React.MouseEvent<HTMLAnchorElement>, href: string) => {
    e.preventDefault();
    if (href.startsWith('#')) {
      const id = href.slice(1);
      const el = document.getElementById(id);
      if (el) {
        el.scrollIntoView({ behavior: 'smooth' });
      }
    }
  };

  return (
    <header className={scrolled ? 'scrolled' : ''}>
      <div className="logo">ALeToys</div>
      <nav>
        <ul>
          <li><a href="#" onClick={(e) => handleAnchor(e, '#') } className="active">Home</a></li>
          <li><a href="#">Collections</a></li>
          <li className='tooltip'>
            <a href="#">Limited Masterpieces</a>
            <span className="tooltipText">Discover our exclusive, limited-edition sculptures.</span>
          </li>
          <li className='tooltip'>
            <a href="#">Buyer's Guide</a>
            <span className="tooltipText">Tips and information for collectors.</span>
          </li>
         <li> <Link href="/shop">About / Contact</Link></li>
        </ul>
      </nav>
      <div className="icons">
        <input type="text" placeholder="Search..." />
        <i className="fas fa-heart" />
        <i className="fas fa-user" />
      </div>
    </header>
  );
}